import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import * as dat from 'dat.gui';


//GUI
const gui = new dat.GUI()
gui.hide()
const debugObjects = {}
debugObjects.sizeAttenuation = false
debugObjects.particleSize = 0.5
debugObjects.vertexCount = 300
debugObjects.color = '#def4ff'
debugObjects.lightColor = '#0077ff'


//Texture Loader
const textureLoader = new THREE.TextureLoader()
const cubeTextureLoader = new THREE.CubeTextureLoader()



//Import
const dracoLoader = new DRACOLoader()
const gltfLoader = new GLTFLoader()
dracoLoader.setDecoderPath('/draco/')
gltfLoader.setDRACOLoader(dracoLoader)



//Mouse
const cursor = {
    x:0,
    y:0
}

window.addEventListener('mousemove', (event)=>{
    cursor.x = (event.clientX/sizes.width) - 0.5
    cursor.y = (event.clientY/sizes.height) - 0.5

    //console.log(cursor.y)

})

//Scroll
let scrollY = 0.01;

window.addEventListener('scroll', ()=>{
    scrollY = window.scrollY/ 400
    console.log(scrollY)

})


/**
 * Sizes
 */
const sizes = {}
sizes.width = window.innerWidth
sizes.height = window.innerHeight

window.addEventListener('resize', () =>
{
    // Save sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

})

/**
 * Environnements
 */
// Scene
const scene = new THREE.Scene()

// Camera
const camera = new THREE.PerspectiveCamera(100, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0,0,6)
scene.add(camera)





//Sphere
const spheres = []
const colors = ['#D939CD', '#3CD500', '#F8D800', '#0396FF', '#7367F0', '#eb1f10']
const geometry = new THREE.SphereBufferGeometry(0.8,24,24)

const generateSphere = () =>{

    for(let i = 0; i < 5; i++){

        const colorRandom = colors[Math.floor(Math.random() * colors.length)]
        const material = new THREE.MeshPhongMaterial({
            color: colorRandom
        })


        const mesh = new THREE.Mesh(
            geometry, material
        )
        scene.add(mesh)
        const randomScale = 1 + Math.random()*2
        mesh.scale.set(randomScale,randomScale,randomScale)
        spheres.push(mesh)
    }

}
generateSphere()
console.log(spheres.length)




//Light

const ambientLight = new THREE.AmbientLight()
ambientLight.intensity = 0.8
scene.add(ambientLight)
gui.add(ambientLight, 'intensity')
    .min(0.1)
    .max(1)
    .step(0.1)
    .name('ambientlightIntensity')



const directionalLight = new THREE.DirectionalLight()
directionalLight.intensity = 1
gui.add(directionalLight, 'intensity')
    .min(0.1)
    .max(1)
    .step(0.1)
    .name('directionalLightIntensity')

gui.add(directionalLight.position, 'x')
    .min(-5)
    .max(5)
    .step(0.1)
    .name('directionalLightX')


directionalLight.shadow.camera.near = 3;
directionalLight.shadow.camera.far = 10;
directionalLight.shadow.camera.top = 3;
directionalLight.shadow.camera.bottom = -2;
directionalLight.shadow.camera.left = 2;
directionalLight.shadow.camera.right = -2;
directionalLight.shadow.mapSize.set(1024, 1024)
directionalLight.shadow.normalBias = 0.05
directionalLight.position.set(5,5,2)
scene.add(directionalLight)
const helper = new THREE.CameraHelper(directionalLight.shadow.camera)
//scene.add(helper)

// Renderer

const canvas = document.querySelector('.webgl')
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true,
    alpha:true
})
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.setSize(sizes.width, sizes.height)


//Shadow
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
directionalLight.castShadow = true






/**
 * Loop
 */
const clock = new THREE.Clock()

const loop = () =>
{
    const elapsedTime = clock.getElapsedTime()

    
    spheres[0].position.x = Math.cos(elapsedTime/2)
    spheres[0].position.y = Math.sin(elapsedTime) + Math.cos(elapsedTime)
    spheres[0].position.z = Math.sin(elapsedTime)


    spheres[1].position.x = Math.cos(elapsedTime/2) * 3
    spheres[1].position.y = Math.sin(elapsedTime/2) * -1 + Math.cos(elapsedTime) * 3
    spheres[1].position.z = Math.sin(elapsedTime/2) * -3


    spheres[2].position.x = Math.cos(elapsedTime/2) * 6 + Math.cos(elapsedTime)
    spheres[2].position.y = Math.sin(elapsedTime/2)* 6 + Math.cos(elapsedTime)


    spheres[3].position.x = Math.sin(elapsedTime/2) * 6
    spheres[3].position.y = Math.cos(elapsedTime/2) * 2
    spheres[3].position.z = Math.sin(elapsedTime/2) * 2


    spheres[4].position.x = Math.sin(elapsedTime/2) * 8
    spheres[4].position.y = Math.cos(elapsedTime/2) * 8
    spheres[4].position.z = Math.sin(elapsedTime/2) * 3



    // Render
    renderer.render(scene, camera)
    

    // Keep looping
    window.requestAnimationFrame(loop)
}
loop()